"use client";

import { useState, useEffect } from "react";
import GameCard from "./game-card";
import { CardType } from "@/types/card";
import { calculateScore } from "@/utils/calculateScore";
import { applyCardEffect } from "@/utils/applyCardEffect";
import { getPlayerScore, updateScore } from "@/utils/firebase";
import { randomCards } from "@/utils/randomCards";

type Phase = "question" | "card" | "popup";

interface Question {
  question: string;
  options: string[];
  answer: string;
}

const QUESTIONS: Question[] = [
  {
    question: "ƒê·∫°i h·ªôi IX c·ªßa ƒê·∫£ng x√°c ƒë·ªãnh m√¥ h√¨nh kinh t·∫ø t·ªïng qu√°t l√† g√¨?",
    options: [
      "Kinh t·∫ø k·∫ø ho·∫°ch h√≥a t·∫≠p trung",
      "Kinh t·∫ø th·ªã tr∆∞·ªùng ƒë·ªãnh h∆∞·ªõng XHCN",
      "Kinh t·∫ø th·ªã tr∆∞·ªùng hi·ªán ƒë·∫°i",
      "Kinh t·∫ø nhi·ªÅu th√†nh ph·∫ßn",
    ],
    answer: "Kinh t·∫ø th·ªã tr∆∞·ªùng ƒë·ªãnh h∆∞·ªõng XHCN",
  },
  {
    question: "ƒê·∫°i h·ªôi VIII x√°c ƒë·ªãnh chi·∫øn l∆∞·ª£c n√†o?",
    options: [
      "C√¥ng nghi·ªáp h√≥a ‚Äì hi·ªán ƒë·∫°i h√≥a",
      "Ph√°t tri·ªÉn n√¥ng nghi·ªáp",
      "M·ªü c·ª≠a h·ªôi nh·∫≠p",
      "Kinh t·∫ø tri th·ª©c",
    ],
    answer: "C√¥ng nghi·ªáp h√≥a ‚Äì hi·ªán ƒë·∫°i h√≥a",
  },
];

function shuffle<T>(arr: T[]) {
  return [...arr].sort(() => Math.random() - 0.5);
}

export default function GamePlay() {
  const [phase, setPhase] = useState<Phase>("question");
  const [score, setScore] = useState(0);

  const [questions, setQuestions] = useState<Question[]>(shuffle(QUESTIONS));
  const [currentIdx, setCurrentIdx] = useState(0);

  const [selectedOption, setSelectedOption] = useState<string | null>(null);
  const [timeLeft, setTimeLeft] = useState(15);

  const CARD_COUNT = 3;
  const MAX_FLIP = 2;

  const [cardOptions, setCardOptions] = useState<CardType[]>([]);
  const [cardsFlipped, setCardsFlipped] = useState<boolean[]>([]);
  const [cardFlipsLeft, setCardFlipsLeft] = useState(MAX_FLIP);
  const [isCardProcessing, setIsCardProcessing] = useState(false);

  // Timer
  useEffect(() => {
    if (phase !== "question" || selectedOption) return;

    if (timeLeft <= 0) {
      setSelectedOption("");
      setTimeout(nextQuestion, 800);
      return;
    }

    const timer = setTimeout(() => setTimeLeft((v) => v - 1), 1000);
    return () => clearTimeout(timer);
  }, [timeLeft, phase, selectedOption]);

  const handleAnswer = (option: string) => {
    if (selectedOption) return;

    setSelectedOption(option);
    const isCorrect = option === currentQuestion.answer;

    if (!isCorrect) {
      setTimeout(nextQuestion, 1200);
      return;
    }

    // Correct answer - add score
    const baseScore = 100;
    const timeBonus = timeLeft * 5;
    const gained = baseScore + timeBonus;
    setScore((prev) => prev + gained);

    // Reset card state for new question
    setCardFlipsLeft(MAX_FLIP);

    // Show popup
    setTimeout(() => {
      setPhase("popup");
    }, 800);
  };

  const handlePickCard = () => {
    // Generate new cards each time
    setCardOptions(randomCards(CARD_COUNT));
    setCardsFlipped(Array(CARD_COUNT).fill(false));
    setPhase("card");
  };

  const handleCardClick = (idx: number) => {
    if (
      isCardProcessing || 
      cardsFlipped[idx] || 
      cardFlipsLeft <= 0
    ) return;
  
    setIsCardProcessing(true); // üîí kh√≥a ngay
  
    setCardsFlipped((prev) =>
      prev.map((v, i) => (i === idx ? true : v))
    );
  
    const card = cardOptions[idx];
  
    setTimeout(() => {
      applyCardEffect(card);
  
      setCardFlipsLeft((prev) => {
        const remaining = prev - 1;
  
        if (remaining > 0) {
          setTimeout(() => {
            setPhase("popup");
            setIsCardProcessing(false); // üîì m·ªü l·∫°i
          }, 800);
        } else {
          setTimeout(() => {
            nextQuestion();
            setIsCardProcessing(false); // üîì m·ªü l·∫°i
          }, 1200);
        }
  
        return remaining;
      });
    }, 500);
  };
  

  const applyCardEffect = (card: CardType) => {
    if (card.startsWith("+")) {
      const val = parseInt(card.substring(1));
      setScore((prev) => prev + val);
    } else if (card.startsWith("-")) {
      const val = parseInt(card.substring(1));
      setScore((prev) => Math.max(0, prev - val));
    } else if (card === "x2") {
      setScore((prev) => prev * 2);
    } else if (card === "x3") {
      setScore((prev) => prev * 3);
    } else if (card === "loseAll") {
      setScore(0);
    }
    
  };

  const nextQuestion = () => {
    setSelectedOption(null);
    setPhase("question");
    setTimeLeft(15);
    setCardOptions([]);
    setCardFlipsLeft(MAX_FLIP); // Reset flips for new question

    if (currentIdx + 1 < questions.length) {
      setCurrentIdx((v) => v + 1);
    } else {
      setQuestions(shuffle(QUESTIONS));
      setCurrentIdx(0);
    }
  };

  const currentQuestion = questions[currentIdx];

if (!currentQuestion) {
  return (
    <div className="min-h-screen flex items-center justify-center">
      <p className="text-gray-500">ƒêang t·∫£i c√¢u h·ªèi...</p>
    </div>
  );
}

  return (
    <div className="min-h-screen bg-gradient-to-br from-indigo-100 to-purple-100 p-6">
      <div className="mx-auto max-w-2xl space-y-6 text-center">
        <div className="bg-white rounded-xl shadow-lg p-4">
          <p className="text-2xl font-bold text-indigo-600">ƒêi·ªÉm: {score}</p>
        </div>

        {/* Question Phase */}
        {phase === "question" && currentQuestion && (
          <div className="p-6 border rounded-xl bg-white shadow-lg">
            <h2 className="text-xl font-semibold mb-2">{currentQuestion.question}</h2>
            <p className="text-sm text-gray-500 mb-4">Th·ªùi gian c√≤n l·∫°i: {timeLeft}s</p>

            <div className="grid grid-cols-2 gap-4">
              {currentQuestion.options.map((opt) => {
                const isCorrect = opt === currentQuestion.answer;
                let style = "bg-gray-100 hover:bg-gray-200";

                if (selectedOption) {
                  if (opt === selectedOption) {
                    style = isCorrect ? "bg-green-500 text-white" : "bg-red-500 text-white";
                  } else if (isCorrect) {
                    style = "bg-green-500 text-white";
                  }
                }

                return (
                  <button
                    key={opt}
                    className={`p-3 rounded font-medium transition-all ${style}`}
                    onClick={() => handleAnswer(opt)}
                    disabled={!!selectedOption}
                  >
                    {opt}
                  </button>
                );
              })}
            </div>
          </div>
        )}

        {/* Popup Phase */}
        {phase === "popup" && (
          <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
            <div className="bg-white rounded-2xl shadow-2xl p-8 max-w-md mx-4 animate-scale-in">
              <h2 className="text-2xl font-bold text-indigo-600 mb-4">üéâ Tr·∫£ l·ªùi ƒë√∫ng!</h2>
              <p className="text-gray-700 mb-6">
                {cardFlipsLeft === MAX_FLIP 
                  ? "B·∫°n c√≥ mu·ªën b√≥c b√†i ƒë·ªÉ nh·∫≠n th∆∞·ªüng th√™m?"
                  : `B·∫°n c√≤n ${cardFlipsLeft} l∆∞·ª£t b√≥c. Ti·∫øp t·ª•c?`}
              </p>
              <div className="flex gap-4">
                <button
                  onClick={handlePickCard}
                  className="flex-1 bg-indigo-600 hover:bg-indigo-700 text-white font-bold py-3 px-6 rounded-lg transition-all"
                >
                  üé¥ B√≥c b√†i
                </button>
                <button
                  onClick={nextQuestion}
                  className="flex-1 bg-gray-300 hover:bg-gray-400 text-gray-800 font-bold py-3 px-6 rounded-lg transition-all"
                >
                  ‚û°Ô∏è C√¢u k·∫ø ti·∫øp
                </button>
              </div>
            </div>
          </div>
        )}

        {/* Card Phase */}
        {phase === "card" && (
          <div className="space-y-4 bg-white rounded-xl shadow-lg p-6">
            <h2 className="text-xl font-bold text-indigo-600">üé¥ Ch·ªçn b√†i ƒë·ªÉ b√≥c</h2>

            <div className="flex justify-center gap-6">
              {cardOptions.map((type, idx) => (
                <GameCard key={idx} type={type} flipped={cardsFlipped[idx]} onClick={() => handleCardClick(idx)} />
              ))}
            </div>

            <p className="text-sm text-gray-500">L∆∞·ª£t l·∫≠t c√≤n l·∫°i: {cardFlipsLeft}</p>
          </div>
        )}
      </div>

      <style jsx>{`
        @keyframes scale-in {
          from {
            transform: scale(0.9);
            opacity: 0;
          }
          to {
            transform: scale(1);
            opacity: 1;
          }
        }
        .animate-scale-in {
          animation: scale-in 0.3s ease-out;
        }
      `}</style>
    </div>
  );
}
